import Vue from 'vue';
import VueRouter from 'vue-router';

import Home from './components/Home';

import Dsc from './components/dsc/Dsc';
import DscDetails from './components/dsc/Dsc-details';
import DscMy from './components/dsc/Dsc-my';
import DscAll from './components/dsc/Dsc-all';
import DscInterfaces from './components/dsc/Dsc-interfaces';

import Dpo from './components/Dpo';
import Ddo from './components/Ddo';
import Rfo from './components/Rfo';

Vue.use(VueRouter);

const router = new VueRouter({
  mode: 'history',
  routes: [
    { path: '/', component: Home, meta: { title: "Home - Data Catalogue" } },
    { path: '/dsc', component: Dsc, meta: { title: "DSC - Data Catalogue" }, children: [
      { path: '', name: 'dsc', redirect: { name: 'dsc.my' } },
      { path: 'my', name: 'dsc.my', component: DscMy, meta: { title: "DSC - Data Catalogue" } },
      { path: 'my/:system', name: 'dsc.my', component: DscMy, meta: { title: "DSC - Data Catalogue" }, children: [
        { path: 'details', name: 'dsc.my.details', component: DscDetails, meta: { title: "DSC Details - Data Catalogue" } },
      ] },
      { path: 'all', name: 'dsc.all', component: DscAll, meta: { title: "DSC - Data Catalogue" } },
      { path: 'all/:system', name: 'dsc.all', component: DscAll, meta: { title: "DSC - Data Catalogue" }, children: [
        { path: 'details', name: 'dsc.all.details', component: DscDetails, meta: { title: "DSC Details - Data Catalogue" } },
      ] },
      { path: 'interfaces', name: 'dsc.interfaces', component: DscInterfaces, meta: { title: "DSC - Data Catalogue" } },
      { path: 'interfaces/:system', name: 'dsc.interfaces', component: DscInterfaces, meta: { title: "DSC - Data Catalogue" }, children: [
        { path: 'details', name: 'dsc.interfaces.details', component: DscDetails, meta: { title: "DSC Details - Data Catalogue" } },
      ] },
    ]},
    { path: '/dpo', component: Dpo, meta: { title: "DPO - Data Catalogue" } },
    { path: '/ddo', component: Ddo, meta: { title: "DDO - Data Catalogue" } },
    { path: '/rfo', component: Rfo, meta: { title: "RFO - Data Catalogue" } }
  ]
});

router.beforeEach((to, from, next) => {
  document.title = (to.meta.title || '')
  next()
});

export default router